.zw-dropdown {
  width: auto;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.1);
  text-align: left;
  will-change: transform, opacity;
  background: #fff;
  z-index: var(--zindex-dropdown);
}
.zw-dropdown[x-placement^=right] {
  margin-left: 4px;
}
.zw-dropdown[x-placement^=top] {
  margin-bottom: 4px;
}
.zw-dropdown[x-placement^=bottom] {
  margin-top: 4px;
}
.zw-dropdown[x-placement^=left] {
  margin-right: 4px;
}
.zw-dropdown--rect {
  border-radius: 0;
}

.zw-dropdown--radius {
  border-radius: 4px;
  overflow: hidden;
}